import { useState } from "react";
import { useSelector } from "react-redux";
import styled from "styled-components";
import DefaultHeader from "../../components/header/DefaultHeader";
import LeftFilter from "../../components/market/LeftFilter";
import { D_categoryList } from "../../data/D_market";
import { putCommaAtPrice } from "../../util/Util";

export default function Minting() {
  const isMobile = useSelector((state) => state.common.isMobile);

  const [category, setCategory] = useState(0);

  if (isMobile)
    return (
      <>
        <DefaultHeader />
        <Mminting></Mminting>
      </>
    );
  else
    return (
      <>
        <DefaultHeader opt="market" />
        <LeftFilter />

        <Pminting>
          <section className="innerBox">
            <ul className="categoryList">
              {D_categoryList.map((v, i) => (
                <li
                  key={i}
                  className={category === i ? "on" : ""}
                  onClick={() => setCategory(i)}
                >
                  {v}
                </li>
              ))}
            </ul>

            <article className="itemArea">
              <p className="total">Total {putCommaAtPrice(3880032)}</p>

              <ul className="itemList">
                {new Array(15).fill(null).map((v, i) => (
                  <li key={i}>
                    <div className="itemBox">
                      <p className="title">Freind #{i + 1}</p>
                    </div>

                    <ul className="infoList">
                      <li>
                        <p className="bid">Highest bid</p>
                        <p className="level">Lv.1</p>
                      </li>

                      <li>
                        <p className="price">2 AKD</p>
                        <p className="score">Score. 245</p>
                      </li>
                    </ul>
                  </li>
                ))}
              </ul>
            </article>
          </section>
        </Pminting>
      </>
    );
}

const Mminting = styled.div`
  background: #252422;
`;

const Pminting = styled.div`
  display: flex;
  height: 100vh;
  padding: 100px 0 0 320px;
  background: #252422;

  .innerBox {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 44px;
    padding: 70px 62px;
    overflow-y: scroll;

    .categoryList {
      display: flex;
      background: rgba(255, 255, 255, 0.1);
      border-radius: 30px;

      li {
        display: inline-flex;
        justify-content: center;
        align-items: center;
        width: 180px;
        height: 54px;
        color: #d8b5d2;
        border-radius: 30px;
        cursor: pointer;
        &.on {
          color: #000;
          background: #fff;
          border: 1.4px solid #d8b5d2;
        }
      }
    }

    .itemArea {
      display: flex;
      flex-direction: column;
      gap: 24px;

      .total {
        font-size: 24px;
        font-weight: 500;
        color: #fff;
      }

      .itemList {
        display: flex;
        flex-wrap: wrap;
        gap: 20px 18px;

        & > li {
          display: flex;
          flex-direction: column;
          width: 270px;
          height: 398px;
          background: #fff;
          border-radius: 12px;
          box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.3);
          overflow: hidden;

          .itemBox {
            flex: 1;
          }

          .infoList {
            display: flex;
            flex-direction: column;
            justify-content: center;
            gap: 4px;
            height: 74px;
            padding: 0 14px;
            background: linear-gradient(
              108.01deg,
              #553f47 32.97%,
              #6a3246 99.39%
            );

            li {
              display: flex;
              justify-content: space-between;
              font-weight: 500;
              color: #fff;

              &:nth-of-type(1) {
                font-size: 14px;
              }

              &:nth-of-type(2) {
                font-size: 16px;

                .price {
                  background: linear-gradient(
                    to right,
                    rgba(245, 195, 220, 100),
                    rgba(245, 195, 220, 80) 40%,
                    rgba(245, 195, 220, 0)
                  );
                  -webkit-background-clip: text;
                  -webkit-text-fill-color: transparent;
                }
              }
            }
          }
        }
      }
    }
  }
`;
