import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router";
import styled from "styled-components";
import { D_navList } from "../../data/D_header";
import I_xWhite from "../../img/icon/I_xWhite.svg";
import T_AKD_R from "../../img/token/T_AKD_R.png";
import T_AKG_R from "../../img/token/T_AKG_R.png";
import { setAddress } from "../../reducers/common";
import SetErrorBar from "../../util/SetErrorBar";
import { strDot } from "../../util/Util";
import PopupBg from "../common/PopupBg";
import ConnectWalletPopup from "../popup/ConnectWalletPopup";

export default function MmorePopup({ off }) {
  const navigate = useNavigate();
  const dispatch = useDispatch();

  const address = useSelector((state) => state.common.address);

  const [connectWalletPopup, setConnectWalletPopup] = useState(false);

  function onClickAddressBtn() {
    dispatch(setAddress({ provider: "", address: "" }));
  }

  function onClickDetNav(url) {
    navigate(url);
    off();
  }

  return (
    <MmorePopupBox>
      <article className="topBar">
        <div className="exitBtnBox">
          <button className="exitBtn" onClick={() => off()}>
            <img src={I_xWhite} alt="" />
          </button>
        </div>

        <div className="contBox">
          <ul className="tokenList">
            <li className="akd">
              <p className="key">AKD</p>
              <p className="value">$1.0</p>
            </li>
            <li className="akg">
              <p className="key">AKG</p>
              <p className="value">$2.3</p>
            </li>
          </ul>
          {address ? (
            <button className="addressBtn" onClick={onClickAddressBtn}>
              <p>{strDot(address, 4, 4)}</p>
            </button>
          ) : (
            <button
              className="connectBtn"
              onClick={() => setConnectWalletPopup(true)}
            >
              <p>Connect Wallet</p>
            </button>
          )}
        </div>
      </article>

      <article className="navArea">
        {D_navList.map((cont, index) => (
          <details className="index" key={index}>
            <summary
              onClick={() => {
                if (!cont.detail[0]) SetErrorBar("준비중입니다");
              }}
            >
              {" "}
              {cont.cover}
            </summary>

            <ul className="detailNav">
              {cont.detail.map((detCont, index) => (
                <li key={index} onClick={() => onClickDetNav(detCont?.url)}>
                  <p>{detCont.text}</p>
                </li>
              ))}
            </ul>
          </details>
        ))}
      </article>

      {connectWalletPopup && (
        <>
          <ConnectWalletPopup off={setConnectWalletPopup} />
          <PopupBg bg blur off={setConnectWalletPopup} />
        </>
      )}
    </MmorePopupBox>
  );
}

const MmorePopupBox = styled.section`
  background: #201d1c;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  position: fixed;
  z-index: 4;

  .topBar {
    height: 48.33vw;
    padding: 6.11vw 5.55vw;
    background: linear-gradient(
      59.81deg,
      #322128 20.72%,
      #583643 58.31%,
      #634752 89.45%
    );

    .exitBtnBox {
      display: flex;
      justify-content: flex-end;

      img {
        width: 4.44vw;
      }
    }

    .contBox {
      display: flex;
      flex-direction: column;
      gap: 3.33vw;
      align-items: center;

      .tokenList {
        display: flex;
        gap: 5.55vw;

        li {
          display: flex;
          align-items: center;
          gap: 2.77vw;
          font-size: 5vw;
          font-weight: 600;
          color: #fff;

          img {
            width: 12.22vw;
            height: 12.22vw;
          }

          p {
          }
        }
      }

      .navArea {
      }

      .connectBtn,
      .addressBtn {
        width: 60.55vw;
        height: 12.22vw;
        border-radius: 8.33vw;
        background: rgba(0, 0, 0, 0.1);
        border: 1.4px solid #f5c3dc;
        border-radius: 8.33vw;

        p {
          font-size: 5vw;
          font-weight: 500;
          background: linear-gradient(
            to right,
            rgba(245, 195, 220, 100),
            rgba(245, 195, 220, 80) 40%,
            rgba(245, 195, 220, 0)
          );
          -webkit-background-clip: text;
          -webkit-text-fill-color: transparent;
        }
      }
    }
  }

  .navArea {
    display: flex;
    flex-direction: column;
    gap: 5vw;
    padding: 12.22vw 11.11vw;

    details {
      summary {
        font-size: 6.66vw;
        font-weight: 600;
        color: #fff;
      }

      .detailNav {
        display: flex;
        flex-direction: column;
        gap: 1.11vw;
        margin: 2.22vw 0 0 0;
        font-size: 5vw;
        font-weight: 500;
        color: #f5c3dc;
      }
    }
  }
`;
