import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router";
import styled from "styled-components";
import I_xGray from "../../img/icon/I_xGray.svg";
import L_metaMask from "../../img/logo/L_metaMask.svg";
import L_klay from "../../img/logo/L_klay.svg";
import L_klip from "../../img/logo/L_klip.png";
import { setAddress } from "../../reducers/common";
import SetErrorBar from "../../util/SetErrorBar";
import { prepare, request, getResult, getCardList } from "klip-sdk";
import { useState } from "react";
import qrcode from "qrcode";
import QrPopup from "./QrPopup";
import { isDeviceMobile } from "../../util/Util";

export default function ConnectWalletPopup({ off }) {
  const navigate = useNavigate();
  const dispatch = useDispatch();

  const isMobile = useSelector((state) => state.common.isMobile);

  const [qrPopup, setQrPopup] = useState(false);

  async function onClickMetaMask() {
    const { ethereum } = window;

    if (!ethereum || !ethereum?.isMetaMask) {
      SetErrorBar("Please Install Metamask");
      return;
    }

    const [address] = await window.ethereum.request({
      method: "eth_requestAccounts",
    });
    dispatch(setAddress({ provider: "metaMask", address }));

    off();
  }

  async function onClickKlaytn() {
    const { klaytn } = window;

    if (!klaytn || !klaytn?.isKaikas) {
      SetErrorBar("Please Install Kaikas");
      return;
    }

    const [address] = await window.klaytn.enable();

    dispatch(setAddress({ provider: "klaytn", address }));

    off();
  }

  async function onClickKlip() {
    const bappName = "ak freinds";
    const successLink = "/success";
    const failLink = "/failed";

    const preRes = await prepare.auth({ bappName, successLink, failLink });

    if (preRes.err) SetErrorBar(preRes.err);
    else request(preRes.request_key, () => setQrPopup(preRes.request_key));

    if (isDeviceMobile) {
      const interverId = setInterval(() => {
        getResult(preRes.request_key).then((res) => {
          if (res.status === "completed") {
            dispatch(
              setAddress({
                provider: "klip",
                address: res.result.klaytn_address,
              })
            );
            off();
          }
        });
      }, 3000);
    }
  }

  function offQrnConnectPopup() {
    setQrPopup();
    off();
  }

  if (isMobile)
    return (
      <>
        <MconnectWalletPopupBox>
          <button className="exitBtn" onClick={() => off()}>
            <img src={I_xGray} alt="" />
          </button>

          <article className="titleArea">
            <p className="title">
              Please connect
              <br />
              an available wallet
            </p>
            <p className="explain">
              Please connect an available
              <br />
              wallet or create a new one
            </p>
          </article>

          <ul className="walletList">
            <li>
              <div className="best">Best</div>
              <button className="walletBtn" onClick={onClickMetaMask}>
                <img src={L_metaMask} alt="" />
              </button>
            </li>

            <li>
              <button className="walletBtn" onClick={onClickKlaytn}>
                <img src={L_klay} alt="" />
              </button>
            </li>

            <li>
              <button className="walletBtn" onClick={onClickKlip}>
                <img src={L_klip} alt="" />
              </button>
            </li>
          </ul>
        </MconnectWalletPopupBox>
      </>
    );
  else
    return (
      <>
        <PconnectWalletPopupBox>
          <button className="exitBtn" onClick={() => off()}>
            <img src={I_xGray} alt="" />
          </button>

          <article className="titleArea">
            <p className="title">Please connect an available wallet</p>
            <p className="explain">
              Please connect an available wallet or create a new one
            </p>
          </article>

          <ul className="walletList">
            <li>
              <div className="best">Best</div>
              <button className="walletBtn" onClick={onClickMetaMask}>
                <img src={L_metaMask} alt="" />
              </button>
            </li>

            <li>
              <button className="walletBtn" onClick={onClickKlaytn}>
                <img src={L_klay} alt="" />
              </button>
            </li>

            <li>
              <button className="walletBtn" onClick={onClickKlip}>
                <img src={L_klip} alt="" />
              </button>
            </li>
          </ul>
        </PconnectWalletPopupBox>

        {qrPopup && <QrPopup resKey={qrPopup} off={offQrnConnectPopup} />}
      </>
    );
}

