import { useState } from "react";
import { useSelector } from "react-redux";
import { useNavigate } from "react-router";
import styled from "styled-components";
import DefaultHeader from "../../components/header/DefaultHeader";
import AkdCollectPopup from "../../components/popup/AkdCollectPopup";
import Stake from "../../components/mine/landing/Stake";
import Unstake from "../../components/mine/landing/Unstake";
import PopupBg from "../../components/common/PopupBg";

export default function StakingDetail() {
  const navigate = useNavigate();

  const isMobile = useSelector((state) => state.common.isMobile);

  const [category, setCategory] = useState(0);
  const [akdCollectionPopup, setAkdCollectionPopup] = useState(false);

  if (isMobile)
    return (
      <>
        <DefaultHeader />
        <MstakingDetailBox className="withBg">
          <section className="innerBox">
            <article className="statusArea">
              <div className="contContainer">
                <strong className="title">Refund status</strong>

                <div className="contBox">
                  <div className="topBar">
                    <p className="key">Your Deposits</p>
                    <p className="value">$0.00</p>
                  </div>

                  <ul className="contList">
                    <li>0 AKD</li>
                    <li>0 AKG</li>
                  </ul>
                </div>
              </div>

              <button
                className="harvestBtn"
                onClick={() => setAkdCollectionPopup(true)}
              >
                Harvest Rewards
              </button>
            </article>

            <article className="contArea">
              <ul className="categoryBar">
                <li
                  className={category === 0 ? "on" : ""}
                  onClick={() => setCategory(0)}
                >
                  Stake
                </li>
                <li
                  className={category === 1 ? "on" : ""}
                  onClick={() => setCategory(1)}
                >
                  UnStake
                </li>
              </ul>

              {category === 0 && <Stake />}
              {category === 1 && <Unstake />}
            </article>
          </section>
        </MstakingDetailBox>

        {akdCollectionPopup && (
          <>
            <AkdCollectPopup off={setAkdCollectionPopup} />
            <PopupBg bg blur off={setAkdCollectionPopup} />
          </>
        )}
      </>
    );
  else
    return (
      <>
        <DefaultHeader />
        <PstakingDetailBox className="withBg">
          <section className="innerBox">
            <article className="statusArea">
              <div className="contContainer">
                <strong className="title">Refund status</strong>

                <div className="contBox">
                  <div className="topBar">
                    <p className="key">Your Deposits</p>
                    <p className="value">$0.00</p>
                  </div>

                  <ul className="contList">
                    <li>0 AKD</li>
                    <li>0 AKG</li>
                  </ul>
                </div>
              </div>

              <button
                className="harvestBtn"
                onClick={() => setAkdCollectionPopup(true)}
              >
                Harvest Rewards
              </button>
            </article>

            <article className="contArea">
              <ul className="categoryBar">
                <li
                  className={category === 0 ? "on" : ""}
                  onClick={() => setCategory(0)}
                >
                  Stake
                </li>
                <li
                  className={category === 1 ? "on" : ""}
                  onClick={() => setCategory(1)}
                >
                  UnStake
                </li>
              </ul>

              {category === 0 && <Stake />}
              {category === 1 && <Unstake />}
            </article>
          </section>
        </PstakingDetailBox>

        {akdCollectionPopup && (
          <>
            <AkdCollectPopup off={setAkdCollectionPopup} />
            <PopupBg bg blur off={setAkdCollectionPopup} />
          </>
        )}
      </>
    );
}

