import { useSelector } from "react-redux";
import styled from "styled-components";
import { D_range, D_partList } from "../../data/D_market";
import I_dnArw from "../../img/icon/I_dnArw.svg";
import I_dnArwWhite from "../../img/icon/I_dnArwWhite.svg";
import { Range, getTrackBackground } from "react-range";
import { useState } from "react";

export default function LeftFilter() {
  const isMobile = useSelector((state) => state.common.isMobile);

  const [levels, setLevels] = useState([0, 10]);
  const [scores, setScores] = useState([0, 10]);

  if (isMobile) return <MleftFilter></MleftFilter>;
  else
    return (
      <PleftFilter>
        <button className="clearBtn" onClick={() => {}}>
          Clear Filter
        </button>

        <article className="contArea">
          <ul className="partList">
            {D_partList.map((v, i) => (
              <details key={i}>
                <summary>
                  <p>{v.cover}</p>
                  <img className="arw off" src={I_dnArw} alt="" />
                  <img className="arw on" src={I_dnArwWhite} alt="" />
                </summary>

                <ul className="detList">
                  {v.part.map((w, j) => (
                    <li key={j}>
                      <p>{`${w.cont} (${w.num})`}</p>
                    </li>
                  ))}
                </ul>
              </details>
            ))}
          </ul>

          <ul className="inputList">
            <li>
              <p className="lavel">Level</p>
              <div className="rangeBox">
                <Range
                  values={levels}
                  step={D_range.step}
                  min={D_range.min}
                  max={D_range.max}
                  onChange={(values) => setLevels(values)}
                  renderTrack={({ props, children }) => (
                    <div
                      className="rangeTrack"
                      onMouseDown={props.onMouseDown}
                      onTouchStart={props.onTouchStart}
                    >
                      <div
                        className="rangeBar"
                        ref={props.ref}
                        style={{
                          background: getTrackBackground({
                            values: levels,
                            colors: ["#4E4A4A", "#CF7B99", "#4E4A4A"],
                            min: D_range.min,
                            max: D_range.max,
                          }),
                        }}
                      >
                        {children}
                      </div>
                    </div>
                  )}
                  renderThumb={({ props, isDragged }) => (
                    <div className="rangeThumb" {...props} />
                  )}
                />
              </div>
              <div className="infoBar">
                <p className="min">{D_range.min}</p>
                <p className="max">{D_range.max}</p>
              </div>
            </li>

            <li>
              <p className="lavel">Score</p>
              <div className="rangeBox">
                <Range
                  values={levels}
                  step={D_range.step}
                  min={D_range.min}
                  max={D_range.max}
                  onChange={(values) => setLevels(values)}
                  renderTrack={({ props, children }) => (
                    <div
                      className="rangeTrack"
                      onMouseDown={props.onMouseDown}
                      onTouchStart={props.onTouchStart}
                    >
                      <div
                        className="rangeBar"
                        ref={props.ref}
                        style={{
                          background: getTrackBackground({
                            values: levels,
                            colors: ["#4E4A4A", "#CF7B99", "#4E4A4A"],
                            min: D_range.min,
                            max: D_range.max,
                          }),
                        }}
                      >
                        {children}
                      </div>
                    </div>
                  )}
                  renderThumb={({ props, isDragged }) => (
                    <div className="rangeThumb" {...props} />
                  )}
                />
              </div>
              <div className="infoBar">
                <p className="min">{D_range.min}</p>
                <p className="max">{D_range.max}</p>
              </div>
            </li>
          </ul>
        </article>
      </PleftFilter>
    );
}

