import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router";
import styled from "styled-components";
import { D_navList } from "../../data/D_header";
import T_AKD_R from "../../img/token/T_AKD_R.png";
import T_AKG_R from "../../img/token/T_AKG_R.png";
import { setAddress } from "../../reducers/common";
import { strDot } from "../../util/Util";
import ConnectWalletPopup from "../popup/ConnectWalletPopup";
import PopupBg from "../common/PopupBg";
import mainBg from "../../img/bg/mainBg.png";
import I_hamburger from "../../img/icon/I_hamburger.svg";
import L_ak from "../../img/logo/L_ak.png";
import MmorePopup from "./MmorePopup";
import SetErrorBar from "../../util/SetErrorBar";

export default function DefaultHeader({ opt }) {
  const navigate = useNavigate();
  const dispatch = useDispatch();

  const isMobile = useSelector((state) => state.common.isMobile);
  const address = useSelector((state) => state.common.address);

  const [connectWalletPopup, setConnectWalletPopup] = useState(false);
  const [mMorePopup, setMmorePopup] = useState(false);

  function onClickAddressBtn() {
    dispatch(setAddress({ provider: "", address: "" }));
  }

  if (isMobile)
    return (
      <>
        <MdefaultHeader>
          <button className="logoBtn" onClick={() => navigate("/")}>
            <img src={L_ak} alt="" />
          </button>

          <button className="moreBtn" onClick={() => setMmorePopup(true)}>
            <img src={I_hamburger} alt="" />
          </button>
        </MdefaultHeader>

        {mMorePopup && <MmorePopup off={setMmorePopup} />}
      </>
    );
  else
    return (
      <>
        <PdefaultHeader
          style={{
            borderBottom:
              opt === "market" && "1px solid rgba(255, 255, 255, 0.2)",
          }}
        >
          <article className="logoBox">
            <button className="logoBtn" onClick={() => navigate("/")}>
              <img src={L_ak} alt="" />
            </button>
          </article>

          <nav>
            {D_navList.map((cont, index) => (
              <li
                key={index}
                onClick={() => {
                  if (!cont.detail[0]) SetErrorBar("준비중입니다");
                }}
              >
                <p className="cover">{cont.cover}</p>

                <ul className="navPopup">
                  {cont.detail.map((detCont, index) => (
                    <li key={index} onClick={() => navigate(detCont.url)}>
                      {detCont.text}
                    </li>
                  ))}
                </ul>
              </li>
            ))}
          </nav>

          <article className="walletBox">
            <ul className="tokenList">
              <li className="akd">
                <p className="key">AKD</p>
                <p className="value">$1.0</p>
              </li>
              <li className="akg">
                <p className="key">AKG</p>
                <p className="value">$2.3</p>
              </li>
            </ul>

            {address ? (
              <button className="addressBtn" onClick={onClickAddressBtn}>
                <p>{strDot(address, 4, 4)}</p>
              </button>
            ) : (
              <button
                className="connectBtn"
                onClick={() => setConnectWalletPopup(true)}
              >
                <p>Connect Wallet</p>
              </button>
            )}
          </article>
        </PdefaultHeader>

        {connectWalletPopup && (
          <>
            <ConnectWalletPopup off={setConnectWalletPopup} />
            <PopupBg bg blur off={setConnectWalletPopup} />
          </>
        )}
      </>
    );
}

const MdefaultHeader = styled.header`
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 58px;
  padding: 0 5.55vw;
  color: #fff;
  top: 0;
  right: 0;
  left: 0;
  position: fixed;
  z-index: 3;

  .logoBtn {
    img {
      width: 44px;
    }
  }
  .moreBtn {
    img {
      width: 20px;
    }
  }
`;

const PdefaultHeader = styled.header`
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100px;
  padding: 0 60px;
  color: #fff;
  top: 0;
  right: 0;
  left: 0;
  position: fixed;
  z-index: 3;

  & > * {
    flex: 1;
  }

  .logoBtn {
    img {
      width: 66px;
    }
  }

  nav {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 34px;

    & > li {
      display: flex;
      justify-content: center;
      align-items: center;
      height: 42px;
      position: relative;

      &:hover {
        .cover {
          text-shadow: 0px 0px 10px rgba(255, 255, 255, 0.67);
        }

        .navPopup {
          display: flex;
        }
      }

      .cover {
        font-size: 18px;
        line-height: 22px;
        font-weight: 500;
      }

      .navPopup {
        display: none;
        flex-direction: column;
        gap: 12px;
        padding: 16px 26px;
        background: #2d2829;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        border-radius: 8px;
        top: 42px;
        position: absolute;

        li {
          font-size: 18px;
          font-weight: 500;
          color: #f5c3dc;
          text-align: center;
          white-space: nowrap;
          cursor: pointer;
        }
      }
    }
  }

  .walletBox {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    gap: 30px;

    .tokenList {
      display: flex;
      align-items: center;
      gap: 18px;

      li {
        display: flex;
        align-items: center;
        gap: 8px;
        font-size: 18px;
        font-weight: 500;
      }
    }

    .addressBtn {
      width: 154px;
      height: 44px;
      font-size: 18px;
      font-weight: 500;
      color: #f5c3dc;
      background: #2c1019;
      border-radius: 30px;
    }

    .connectBtn {
      width: 170px;
      height: 44px;
      font-size: 18px;
      font-weight: 500;
      background: rgba(0, 0, 0, 0.1);
      border: 1.4px solid #f5c3dc;
      border-radius: 30px;

      p {
        background: linear-gradient(
          to right,
          rgba(245, 195, 220, 100),
          rgba(245, 195, 220, 80) 40%,
          rgba(245, 195, 220, 0)
        );
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
      }
    }
  }
`;
