import WaitingComfirmationPopup from "../popup/WaitingComfirmPopup";
import TransactionSubmitPopup from "../popup/TransactionSubmitPopup";
import PopupBg from "../common/PopupBg";

export default function Sell() {
  const navigate = useNavigate();
  const dispatch = useDispatch();

  const isMobile = useSelector((state) => state.common.isMobile);

  const [selectTokenPopup, setSelectTokenPopup] = useState(false);
  const [selectToken, setSelectToken] = useState(D_tokenList[0]);
  const [fromPrice, setFromPrice] = useState("");
  const [toPrice, setToPrice] = useState("");
  const [waitingConfirmPopup, setWaitingConfirmPopup] = useState(false);
  const [transactionSubmitPopup, setTransactionSubmitPopup] = useState(false);

  if (isMobile)
    return (
      <>
        <MsellBox>
          <ul className="inputList">
            <li className="fromBox">
              <p className="balance">Balance 0.000000</p>

              <div className="inputContainer">
                <div className="inputBox">
                  <input
                    value={fromPrice}
                    onChange={(e) => setFromPrice(e.target.value)}
                    placeholder="0.0"
                  />
                </div>
              </div>
            </li>

            <li className="tokenContainer toBox">
              <p className="balance">Balance 0.000000</p>

              <div className="inputContainer">
                <button
                  className="tokenBtn"
                  onClick={() => setSelectTokenPopup(true)}
                >
                  <img className="tokenImg" src={selectToken.img} alt="" />
                  <p>{selectToken.text}</p>
                  <img className="arw" src={I_dnArwGray} alt="" />
                </button>

                <div className="inputBox">
                  <input
                    value={toPrice}
                    onChange={(e) => setToPrice(e.target.value)}
                    placeholder="0.0"
                  />

                  <button className="maxBtn" onClick={() => {}}>
                    MAX
                  </button>
                </div>
              </div>
            </li>
          </ul>

          <button
            className="actionBtn buyBtn"
            onClick={() => setWaitingConfirmPopup(true)}
            disabled={!(fromPrice && toPrice)}
          >
            {fromPrice && toPrice ? "Sell" : "Enter an amount"}
          </button>
        </MsellBox>

        {waitingConfirmPopup && (
          <>
            <WaitingComfirmationPopup
              off={setWaitingConfirmPopup}
              nextProcess={setTransactionSubmitPopup}
            />
            <PopupBg bg blur off={setWaitingConfirmPopup} />
          </>
        )}

        {transactionSubmitPopup && (
          <>
            <TransactionSubmitPopup off={setTransactionSubmitPopup} />
            <PopupBg bg blur off={setTransactionSubmitPopup} />
          </>
        )}

        {selectTokenPopup && (
          <>
            <SelectTokenPopup
              off={setSelectTokenPopup}
              selectToken={selectToken}
              setSelectToken={setSelectToken}
            />
            <PopupBg bg blur off={setSelectTokenPopup} />
          </>
        )}
      </>
    );
  else
    return (
      <>
        <PsellBox>
          <ul className="inputList">
            <li className="fromBox">
              <p className="balance">Balance 0.000000</p>

              <div className="inputContainer">
                <div className="inputBox">
                  <input
                    value={fromPrice}
                    onChange={(e) => setFromPrice(e.target.value)}
                    placeholder="0.0"
                  />
                </div>
              </div>
            </li>

            <li className="tokenContainer toBox">
              <p className="balance">Balance 0.000000</p>

              <div className="inputContainer">
                <button
                  className="tokenBtn"
                  onClick={() => setSelectTokenPopup(true)}
                >
                  <img className="tokenImg" src={selectToken.img} alt="" />
                  <p>{selectToken.text}</p>
                  <img className="arw" src={I_dnArwGray} alt="" />
                </button>

                <div className="inputBox">
                  <input
                    value={toPrice}
                    onChange={(e) => setToPrice(e.target.value)}
                    placeholder="0.0"
                  />

                  <button className="maxBtn" onClick={() => {}}>
                    MAX
                  </button>
                </div>
              </div>
            </li>
          </ul>

          <button
            className="actionBtn buyBtn"
            onClick={() => setWaitingConfirmPopup(true)}
            disabled={!(fromPrice && toPrice)}
          >
            {fromPrice && toPrice ? "Sell" : "Enter an amount"}
          </button>
        </PsellBox>

        {waitingConfirmPopup && (
          <>
            <WaitingComfirmationPopup
              off={setWaitingConfirmPopup}
              nextProcess={setTransactionSubmitPopup}
            />
            <PopupBg bg blur off={setWaitingConfirmPopup} />
          </>
        )}

        {transactionSubmitPopup && (
          <>
            <TransactionSubmitPopup off={setTransactionSubmitPopup} />
            <PopupBg bg blur off={setTransactionSubmitPopup} />
          </>
        )}

        {selectTokenPopup && (
          <>
            <SelectTokenPopup
              off={setSelectTokenPopup}
              selectToken={selectToken}
              setSelectToken={setSelectToken}
            />
            <PopupBg bg blur off={setSelectTokenPopup} />
          </>
        )}
      </>
    );
}

