import { D_tokenList } from "../../../data/D_common";
import WaitingComfirmationPopup from "../../popup/WaitingComfirmPopup";
import TransactionSubmitPopup from "../../popup/TransactionSubmitPopup";
import T_AKD_R from "../../../img/token/T_AKD_R.png";
import T_AKG_R from "../../../img/token/T_AKG_R.png";
import PopupBg from "../../common/PopupBg";

export default function Stake() {
  const navigate = useNavigate();
  const dispatch = useDispatch();

  const isMobile = useSelector((state) => state.common.isMobile);

  const [selectTokenPopup, setSelectTokenPopup] = useState(false);
  const [selectToken, setSelectToken] = useState(D_tokenList[0]);
  const [price, setPrice] = useState("");
  const [waitingConfirmPopup, setWaitingConfirmPopup] = useState(false);
  const [transactionSubmitPopup, setTransactionSubmitPopup] = useState(false);

  if (isMobile)
    return (
      <>
        <MstakeBox>
          <div className="topBar">
            <div className="imgContainer">
              <div className="imgBox">
                <img src={T_AKD_R} alt="" />
              </div>
              <div className="imgBox">
                <img src={T_AKG_R} alt="" />
              </div>
            </div>

            <strong className="title">AKD-AKG</strong>
          </div>

          <ul className="inputList">
            <li className="pricdBox">
              <p className="balance">Balance 0.000000 AKD + AKG </p>

              <div className="inputContainer">
                <div className="inputBox">
                  <input
                    value={price}
                    onChange={(e) => setPrice(e.target.value)}
                    placeholder="0.0"
                  />
                </div>
              </div>
            </li>
          </ul>

          <button
            className="actionBtn"
            onClick={() => setWaitingConfirmPopup(true)}
            disabled={!price}
          >
            {price ? "Confirm" : "Enter an amount"}
          </button>
        </MstakeBox>

        {waitingConfirmPopup && (
          <>
            <WaitingComfirmationPopup
              off={setWaitingConfirmPopup}
              nextProcess={setTransactionSubmitPopup}
            />
            <PopupBg bg blur off={setWaitingConfirmPopup} />
          </>
        )}

        {transactionSubmitPopup && (
          <>
            <TransactionSubmitPopup off={setTransactionSubmitPopup} />
            <PopupBg bg blur off={setTransactionSubmitPopup} />
          </>
        )}

        {selectTokenPopup && (
          <>
            <SelectTokenPopup
              off={setSelectTokenPopup}
              selectToken={selectToken}
              setSelectToken={setSelectToken}
            />
            <PopupBg bg blur off={setSelectTokenPopup} />
          </>
        )}
      </>
    );
  else
    return (
      <>
        <PstakeBox>
          <div className="topBar">
            <div className="imgContainer">
              <div className="imgBox">
                <img src={T_AKD_R} alt="" />
              </div>
              <div className="imgBox">
                <img src={T_AKG_R} alt="" />
              </div>
            </div>

            <strong className="title">AKD-AKG</strong>
          </div>
          <ul className="inputList">
            <li className="pricdBox">
              <p className="balance">Balance 0.000000 AKD + AKG </p>

              <div className="inputContainer">
                <div className="inputBox">
                  <input
                    value={price}
                    onChange={(e) => setPrice(e.target.value)}
                    placeholder="0.0"
                  />

                  <button className="maxBtn" onClick={() => {}}>
                    MAX
                  </button>
                </div>
              </div>
            </li>
          </ul>

          <button
            className="actionBtn"
            onClick={() => setWaitingConfirmPopup(true)}
            disabled={!price}
          >
            {price ? "Confirm" : "Enter an amount"}
          </button>
        </PstakeBox>

        {waitingConfirmPopup && (
          <>
            <WaitingComfirmationPopup
              off={setWaitingConfirmPopup}
              nextProcess={setTransactionSubmitPopup}
            />
            <PopupBg bg blur off={setWaitingConfirmPopup} />
          </>
        )}

        {transactionSubmitPopup && (
          <>
            <TransactionSubmitPopup off={setTransactionSubmitPopup} />
            <PopupBg bg blur off={setTransactionSubmitPopup} />
          </>
        )}

        {selectTokenPopup && (
          <>
            <SelectTokenPopup
              off={setSelectTokenPopup}
              selectToken={selectToken}
              setSelectToken={setSelectToken}
            />
            <PopupBg bg blur off={setSelectTokenPopup} />
          </>
        )}
      </>
    );
}

