import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router";
import styled from "styled-components";
import I_xWhite from "../../img/icon/I_xWhite.svg";
import T_AKG from "../../img/token/T_AKG.png";

export default function ClaimTransactionPopup({ off, nextProcess }) {
  const navigate = useNavigate();
  const dispatch = useDispatch();

  const isMobile = useSelector((state) => state.common.isMobile);

  function onClickConfirm() {
    off();
    nextProcess();
  }

  if (isMobile)
    return (
      <>
        <MclaimTransactionPopup>
          <article className="topBar">
            <strong className="title">Claim Transaction</strong>
            <button className="exitBtn" onClick={() => off()}>
              <img src={I_xWhite} alt="" />
            </button>
          </article>

          <article className="contArea">
            <div className="contBox">
              <div className="tokenBox">
                <img src={T_AKG} alt="" />
                <strong>AKD</strong>
              </div>

              <div className="priceBox">
                <strong className="price">0.420 AKD</strong>
                <p className="exchange">~$10.79</p>
              </div>
            </div>

            <button className="confirmBtn" onClick={onClickConfirm}>
              Confirm
            </button>
          </article>
        </MclaimTransactionPopup>
      </>
    );
  else
    return (
      <>
        <PclaimTransactionPopup>
          <article className="topBar">
            <strong className="title">Claim Transaction</strong>
            <button className="exitBtn" onClick={() => off()}>
              <img src={I_xWhite} alt="" />
            </button>
          </article>

          <article className="contArea">
            <div className="contBox">
              <div className="tokenBox">
                <img src={T_AKG} alt="" />
                <strong>AKD</strong>
              </div>

              <div className="priceBox">
                <strong className="price">0.420 AKD</strong>
                <p className="exchange">~$10.79</p>
              </div>
            </div>

            <button className="confirmBtn" onClick={onClickConfirm}>
              Confirm
            </button>
          </article>
        </PclaimTransactionPopup>
      </>
    );
}

