import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router";
import styled from "styled-components";
import I_xWhite from "../../img/icon/I_xWhite.svg";
import I_upArw3 from "../../img/icon/I_upArw3.svg";
import I_link from "../../img/icon/I_link.svg";
import I_searchWhite from "../../img/icon/I_searchWhite.svg";
import { useEffect, useState } from "react";
import { D_tokenList } from "../../data/D_common";

export default function SelectTokenPopup({ off, selectToken, setSelectToken }) {
  const navigate = useNavigate();
  const dispatch = useDispatch();

  const isMobile = useSelector((state) => state.common.isMobile);

  const [search, setSearch] = useState("");

  function onClickToken(cont) {
    setSelectToken(cont);
    off();
  }

  if (isMobile)
    return (
      <>
        <MselectTokenPopup>
          <article className="topBar">
            <strong className="title">Select a token</strong>

            <button className="exitBtn" onClick={() => off()}>
              <img src={I_xWhite} alt="" />
            </button>
          </article>

          <article className="contArea">
            <div className="searchBox">
              <div className="inputBox">
                <input
                  value={search}
                  onChange={(e) => setSearch(e.target.value)}
                  placeholder="Search name or paste address"
                />

                <button className="searchBtn" onClick={() => {}}>
                  <img src={I_searchWhite} alt="" />
                </button>
              </div>
            </div>

            <ul className="tokenList">
              {D_tokenList.map((cont, index) => (
                <li
                  className={cont.text === selectToken.text ? "on" : ""}
                  key={index}
                  onClick={() => onClickToken(cont)}
                >
                  <img src={cont.img} alt="" />
                  <p>{cont.text}</p>
                </li>
              ))}
            </ul>
          </article>
        </MselectTokenPopup>
      </>
    );
  else
    return (
      <>
        <PselectTokenPopup>
          <article className="topBar">
            <span className="blank" />

            <strong className="title">Select a token</strong>

            <button className="exitBtn" onClick={() => off()}>
              <img src={I_xWhite} alt="" />
            </button>
          </article>

          <article className="contArea">
            <div className="searchBox">
              <div className="inputBox">
                <input
                  value={search}
                  onChange={(e) => setSearch(e.target.value)}
                  placeholder="Search name or paste address"
                />

                <button className="searchBtn" onClick={() => {}}>
                  <img src={I_searchWhite} alt="" />
                </button>
              </div>
            </div>

            <div className="scrollBox">
              <ul className="tokenList">
                {D_tokenList.map((cont, index) => (
                  <li
                    className={cont.text === selectToken.text ? "on" : ""}
                    key={index}
                    onClick={() => onClickToken(cont)}
                  >
                    <img src={cont.img} alt="" />
                    <p>{cont.text}</p>
                  </li>
                ))}
              </ul>
            </div>
          </article>
        </PselectTokenPopup>
      </>
    );
}

