import SelectFreindPopup from "../../components/popup/SelectFreindPopup";
import UnstakeLiquidityPopup from "../../components/popup/UnstakeLiquidityPopup";
import E_item from "../../img/example/E_item.png";

export default function Parking() {
  const navigate = useNavigate();
  const dispatch = useDispatch();

  const isMobile = useSelector((state) => state.common.isMobile);

  const [claimPopup, setClaimPopup] = useState(false);
  const [unStakePopup, setUnStakePopup] = useState(false);
  const [selectFriendPopup, setSelectFriendPopup] = useState(false);

  if (isMobile)
    return (
      <>
        <DefaultHeader />
        <MparkingBox className="withBg">
          <section className="innerBox">
            <article className="infoArea">
              <p className="title">Claim</p>

              <div className="infoContainer">
                <ul className="infoList">
                  <li>
                    <p className="key">Total parked Friends:</p>
                    <strong className="value">x</strong>
                  </li>

                  <li>
                    <p className="key">Total staked AKG:</p>
                    <strong className="value">X ($X.XX)</strong>
                  </li>

                  <li>
                    <p className="key">Estimated APR:</p>
                    <p className="value">150%</p>
                  </li>

                  <li>
                    <p className="key">Total pending reward:</p>
                    <p className="value">10 AKG</p>
                  </li>

                  <li>
                    <p className="key">Total claimed reward:</p>
                    <p className="value">0 AKG</p>
                  </li>
                </ul>

                <button
                  className="claimBtn"
                  onClick={() => setClaimPopup(true)}
                  disabled={true}
                >
                  <p>Claim All</p>
                </button>
              </div>
            </article>

            <article className="stakeArea">
              <p className="title">Get your AKG now</p>

              <button
                className="stakeBtn"
                onClick={() => setSelectFriendPopup(true)}
                disabled={true}
              >
                Stake
              </button>

              <ul className="itemList">
                {[1, 2, 3, 4, 5, 6].map((cont, index) => (
                  <li key={index}>
                    <img src={E_item} alt="" />
                    <p>AK Score : 1846.25</p>
                  </li>
                ))}
              </ul>
            </article>
          </section>
        </MparkingBox>

        {claimPopup && (
          <>
            <ClaimTransactionPopup
              off={setClaimPopup}
              nextProcess={() => setUnStakePopup(true)}
            />
            <PopupBg bg blur off={setClaimPopup} />
          </>
        )}

        {unStakePopup && (
          <>
            <UnstakeLiquidityPopup off={setUnStakePopup} />
            <PopupBg bg blur off={setUnStakePopup} />
          </>
        )}

        {selectFriendPopup && (
          <>
            <SelectFreindPopup off={setSelectFriendPopup} />
            <PopupBg bg blur off={setSelectFriendPopup} />
          </>
        )}
      </>
    );
  else
    return (
      <>
        <DefaultHeader />
        <PparkingBox className="withBg">
          <section className="innerBox">
            <article className="infoArea">
              <p className="title">Claim</p>

              <div className="infoContainer">
                <div className="infoListBox">
                  <ul>
                    <li>
                      <p className="key">Total parked Friends:</p>
                      <strong className="value">x</strong>
                    </li>
                    <li>
                      <p className="key">Total staked AKG:</p>
                      <strong className="value">X ($X.XX)</strong>
                    </li>
                  </ul>
                  <ul>
                    <li>
                      <p className="key">Estimated APR:</p>
                      <p className="value">150%</p>
                    </li>
                    <li>
                      <p className="key">Total pending reward:</p>
                      <p className="value">10 AKG</p>
                    </li>
                    <li>
                      <p className="key">Total claimed reward:</p>
                      <p className="value">0 AKG</p>
                    </li>
                  </ul>
                </div>

                <button
                  className="claimBtn"
                  onClick={() => setClaimPopup(true)}
                  disabled={true}
                >
                  <p>Claim All</p>
                </button>
              </div>
            </article>

            <article className="stakeArea">
              <p className="title">Get your AKG now</p>

              <button
                className="stakeBtn"
                onClick={() => setSelectFriendPopup(true)}
                disabled={true}
              >
                Stake
              </button>

              <ul className="itemList">
                {[1, 2, 3, 4, 5, 6].map((cont, index) => (
                  <li key={index}>
                    <img src={E_item} alt="" />
                    <p>AK Score : 1846.25</p>
                  </li>
                ))}
              </ul>
            </article>
          </section>
        </PparkingBox>

        {claimPopup && (
          <>
            <ClaimTransactionPopup
              off={setClaimPopup}
              nextProcess={() => setUnStakePopup(true)}
            />
            <PopupBg bg blur off={setClaimPopup} />
          </>
        )}

        {unStakePopup && (
          <>
            <UnstakeLiquidityPopup off={setUnStakePopup} />
            <PopupBg bg blur off={setUnStakePopup} />
          </>
        )}

        {selectFriendPopup && (
          <>
            <SelectFreindPopup off={setSelectFriendPopup} />
            <PopupBg bg blur off={setSelectFriendPopup} />
          </>
        )}
      </>
    );
}

