import { useState } from "react";
import { useSelector } from "react-redux";
import { useNavigate } from "react-router";
import styled from "styled-components";
import DefaultHeader from "../../components/header/DefaultHeader";
import { putCommaAtPrice } from "../../util/Util";
import Buy from "../../components/akd/Buy";
import Sell from "../../components/akd/Sell";
import { D_refundStatusList } from "../../data/D_akd";

export default function Akd() {
  const navigate = useNavigate();

  const isMobile = useSelector((state) => state.common.isMobile);

  const [category, setCategory] = useState(0);

  if (isMobile)
    return (
      <>
        <DefaultHeader />
        <MakdBox className="withBg">
          <section className="innerBox">
            <article className="totalBox">
              <p className="key">AKD total supply</p>
              <strong className="value">${putCommaAtPrice(2000000)}</strong>
            </article>

            <article className="contArea">
              <ul className="categoryBar">
                <li
                  className={category === 0 ? "on" : ""}
                  onClick={() => setCategory(0)}
                >
                  Buy
                </li>
                <li
                  className={category === 1 ? "on" : ""}
                  onClick={() => setCategory(1)}
                >
                  Sell
                </li>
              </ul>

              {category === 0 && <Buy />}
              {category === 1 && <Sell />}
            </article>

            {category === 1 && (
              <article className="refundArea">
                <strong className="title">Refund status</strong>

                <div className="listBox">
                  <ul className="list">
                    {D_refundStatusList.map((cont, index) => (
                      <li key={index}>
                        <ul className="infoList">
                          <li>
                            <p className="key">Refund</p>
                            <p className="value">KUSDT</p>
                          </li>
                          <li>
                            <p className="key">Unlock</p>
                            <p className="value">{putCommaAtPrice(20000)}</p>
                          </li>
                          <li>
                            <p className="key">Refundable (7days)</p>
                            <p className="value">{putCommaAtPrice(20000)}</p>
                          </li>
                        </ul>

                        <button
                          className="unlockBtn"
                          disabled={!cont.refundable.able}
                          onClick={() => {}}
                        >
                          Unlock
                        </button>
                      </li>
                    ))}
                  </ul>
                </div>
              </article>
            )}
          </section>
        </MakdBox>
      </>
    );
  else
    return (
      <>
        <DefaultHeader />
        <PakdBox className="withBg">
          <section className="innerBox">
            <article className="totalBox">
              <p className="key">AKD total supply</p>
              <strong className="value">${putCommaAtPrice(2000000)}</strong>
            </article>

            <article className="contArea">
              <ul className="categoryBar">
                <li
                  className={category === 0 ? "on" : ""}
                  onClick={() => setCategory(0)}
                >
                  Buy
                </li>
                <li
                  className={category === 1 ? "on" : ""}
                  onClick={() => setCategory(1)}
                >
                  Sell
                </li>
              </ul>

              {category === 0 && <Buy />}
              {category === 1 && <Sell />}
            </article>

            {category === 1 && (
              <article className="refundArea">
                <strong className="title">Refund status</strong>

                <div className="listBox">
                  <ul className="listHeader">
                    <li>Refund</li>
                    <li>Unlock</li>
                    <li>Refundable (7days)</li>
                  </ul>

                  <ul className="list">
                    {D_refundStatusList.map((cont, index) => (
                      <li key={index}>
                        <span>{cont.refund}</span>
                        <span>{cont.unlock}</span>
                        <span>
                          <p>{cont.refundable?.price}</p>
                          <button
                            className="unlockBtn"
                            disabled={!cont.refundable.able}
                            onClick={() => {}}
                          >
                            Unlock
                          </button>
                        </span>
                      </li>
                    ))}
                  </ul>
                </div>
              </article>
            )}
          </section>
        </PakdBox>
      </>
    );
}

