export default function Staking() {
  const navigate = useNavigate();
  const dispatch = useDispatch();

  const isMobile = useSelector((state) => state.common.isMobile);

  function onClickManage(url) {
    if (url) navigate(url);
    else SetErrorBar('준비중입니다')
  }

  if (isMobile)
    return (
      <>
        <DefaultHeader />
        <MstakingBox className="withBg">
          <section className="innerBox">
            <div className="pageTitleBox">
              <strong className="title">Mine</strong>

              <strong className="explain">Stake LP tokens to earn. </strong>
            </div>

            <ul className="contList">
              {D_stakingList.map((cont, index) => (
                <li key={index}>
                  <div className="topBar">
                    <div className="imgContainer">
                      {cont.img.map((img, imgIndex) => (
                        <div className="imgBox" key={imgIndex}>
                          <img src={img} alt="" />
                        </div>
                      ))}
                    </div>

                    <strong className="title">{cont.title}</strong>
                  </div>

                  <ul className="infoList">
                    <li>
                      <p className="key">TVL:</p>
                      <p className="value">{cont.TVL}</p>
                    </li>

                    <li>
                      <p className="key">APR:</p>
                      <p className="value">{cont.APR}%</p>
                    </li>

                    <li>
                      <p className="key">Earn:</p>
                      <p className="value">{cont.Earn}</p>
                    </li>

                    <li>
                      <p className="key">Your Staked:</p>
                      <p className="value">{cont.staked.toFixed(6)} AKD</p>
                    </li>
                  </ul>

                  <button
                    className="manageBtn"
                    onClick={() => onClickManage(cont?.url)}
                  >
                    <p>Mange</p>
                  </button>
                </li>
              ))}
            </ul>
          </section>
        </MstakingBox>
      </>
    );
  else
    return (
      <>
        <DefaultHeader />
        <PstakingBox className="withBg">
          <section className="innerBox">
            <div className="pageTitleBox">
              <strong className="title">Mine</strong>

              <strong className="explain">Stake LP tokens to earn. </strong>
            </div>

            <ul className="contList">
              {D_stakingList.map((cont, index) => (
                <li key={index}>
                  <div className="topBar">
                    <div className="imgContainer">
                      {cont.img.map((img, imgIndex) => (
                        <div className="imgBox" key={imgIndex}>
                          <img src={img} alt="" />
                        </div>
                      ))}
                    </div>

                    <strong className="title">{cont.title}</strong>
                  </div>

                  <ul className="infoList">
                    <li>
                      <p className="key">TVL:</p>
                      <p className="value">{cont.TVL}</p>
                    </li>

                    <li>
                      <p className="key">APR:</p>
                      <p className="value">{cont.APR}%</p>
                    </li>

                    <li>
                      <p className="key">Earn:</p>
                      <p className="value">{cont.Earn}</p>
                    </li>

                    <li>
                      <p className="key">Your Staked:</p>
                      <p className="value">{cont.staked.toFixed(6)} AKD</p>
                    </li>
                  </ul>

                  <button
                    className="manageBtn"
                    onClick={() => navigate(cont?.url)}
                  >
                    <p>Mange</p>
                  </button>
                </li>
              ))}
            </ul>
          </section>
        </PstakingBox>
      </>
    );
}

