import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router";
import styled from "styled-components";
import I_xWhite from "../../img/icon/I_xWhite.svg";
import I_spinner from "../../img/icon/I_spinner.png";
import { useEffect, useLayoutEffect, useState } from "react";
import { prepare, request, getResult, getCardList } from "klip-sdk";
import qrcode from "qrcode";
import { setAddress } from "../../reducers/common";

export default function QrPopup({ resKey, off }) {
  const dispatch = useDispatch();

  const isMobile = useSelector((state) => state.common.isMobile);

  const [qr, setQr] = useState("");

  async function fetchQr() {
    setQr(
      await qrcode.toDataURL(
        `https://klipwallet.com/?target=/a2a?request_key=${resKey}`
      )
    );
  }

  useLayoutEffect(() => {
    fetchQr();
  }, []);

  useEffect(() => {
    const interverId = setInterval(() => {
      getResult(resKey).then((res) => {
        if (res.status === "completed") {
          dispatch(
            setAddress({ provider: "klip", address: res.result.klaytn_address })
          );
          clearInterval(interverId);
          off();
        }
      });
    }, 1000);
  }, []);

  if (isMobile)
    return (
      <>
        <MqrPopup></MqrPopup>
      </>
    );
  else
    return (
      <>
        <PqrPopup>
          <article className="topBar">
            <strong className="title">Qr</strong>
            <button className="exitBtn" onClick={() => off()}>
              <img src={I_xWhite} alt="" />
            </button>
          </article>

          <article className="contArea">
            <img className="qr" src={qr} alt="" />
            <p className="explain">Please Scan Qr Code</p>
          </article>
        </PqrPopup>
      </>
    );
}

const MqrPopup = styled.div``;

const PqrPopup = styled.div`
  width: 380px;
  background: linear-gradient(
    59.81deg,
    #322128 20.72%,
    rgba(88, 54, 67, 0.67) 58.31%,
    #634752 89.45%
  );
  border: 1px solid #f5c3dc;
  border-radius: 24px;
  top: 50%;
  left: 50%;
  position: fixed;
  z-index: 6;
  transform: translate(-50%, -50%);

  .topBar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 72px;
    padding: 0 30px;

    .title {
      font-size: 16px;
      font-weight: 600;
      color: #fff;
    }

    .exitBtn {
      img {
        width: 16px;
      }
    }
  }

  .contArea {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 24px;
    padding: 24px;

    .qr {
      width: 200px;
      border-radius: 10px;
    }

    .explain {
      font-size: 14px;
      font-weight: 600;
      background: linear-gradient(
        to right,
        rgba(245, 195, 220, 1),
        rgba(245, 195, 220, 1) 40%,
        rgba(245, 195, 220, 0.2)
      );
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
    }
  }
`;
