export default function SetTransactionPopup({ off }) {
  const isMobile = useSelector((state) => state.common.isMobile);

  const [percent, setPercent] = useState("");
  const [time, setTime] = useState("");

  if (isMobile)
    return (
      <>
        <MsetTransactionPopup>
          <article className="topBar">
            <strong className="title">Transaction settings</strong>

            <button className="exitBtn" onClick={() => off()}>
              <img src={I_xWhite} alt="" />
            </button>
          </article>

          <article className="contArea">
            <div className="percentBox optBox">
              <div className="titleBox">
                <p className="title">Slippage tolerance</p>
                <button className="infoBtn" onClick={() => {}}>
                  <img src={I_info} alt="" />
                </button>
              </div>

              <ul className="optList">
                <li className={percent * 1 === 0.1 ? "on" : ""}>
                  <button onClick={() => setPercent(0.1)}>0.1%</button>
                </li>
                <li className={percent * 1 === 0.5 ? "on" : ""}>
                  <button onClick={() => setPercent(0.5)}>0.5%</button>
                </li>
                <li className={percent * 1 === 1 ? "on" : ""}>
                  <button onClick={() => setPercent(1)}>1%</button>
                </li>

                <li className="inputBox">
                  <input
                    type="number"
                    value={percent}
                    onChange={(e) => setPercent(e.target.value)}
                    placeholder="0.50"
                  />
                  %
                </li>
              </ul>
            </div>

            <div className="timeBox optBox">
              <div className="titleBox">
                <p className="title">Slippage tolerance</p>
                <button className="infoBtn" onClick={() => {}}>
                  <img src={I_info} alt="" />
                </button>
              </div>

              <div className="inputBox">
                <input
                  className="value"
                  value={time}
                  onChange={(e) => setTime(e.target.value)}
                  placeholder="0"
                />

                <p className="key">minutes</p>
              </div>
            </div>
          </article>
        </MsetTransactionPopup>
      </>
    );
  else
    return (
      <>
        <PsetTransactionPopup>
          <article className="topBar">
            <span className="blank" />

            <strong className="title">Transaction settings</strong>

            <button className="exitBtn" onClick={() => off()}>
              <img src={I_xWhite} alt="" />
            </button>
          </article>

          <article className="contArea">
            <div className="percentBox optBox">
              <div className="titleBox">
                <p className="title">Slippage tolerance</p>
                <button className="infoBtn" onClick={() => {}}>
                  <img src={I_info} alt="" />
                </button>
              </div>

              <ul className="optList">
                <li className={percent * 1 === 0.1 ? "on" : ""}>
                  <button onClick={() => setPercent(0.1)}>0.1%</button>
                </li>
                <li className={percent * 1 === 0.5 ? "on" : ""}>
                  <button onClick={() => setPercent(0.5)}>0.5%</button>
                </li>
                <li className={percent * 1 === 1 ? "on" : ""}>
                  <button onClick={() => setPercent(1)}>1%</button>
                </li>

                <li className="inputBox">
                  <input
                    type="number"
                    value={percent}
                    onChange={(e) => setPercent(e.target.value)}
                    placeholder="0.50"
                  />
                  %
                </li>
              </ul>
            </div>

            <div className="timeBox optBox">
              <div className="titleBox">
                <p className="title">Slippage tolerance</p>
                <button className="infoBtn" onClick={() => {}}>
                  <img src={I_info} alt="" />
                </button>
              </div>

              <div className="inputBox">
                <input
                  className="value"
                  value={time}
                  onChange={(e) => setTime(e.target.value)}
                  placeholder="0"
                />

                <p className="key">minutes</p>
              </div>
            </div>
          </article>
        </PsetTransactionPopup>
      </>
    );
}

