export default function UnstakeLiquidityPopup({ off, nextProcess }) {
  const navigate = useNavigate();
  const dispatch = useDispatch();

  const isMobile = useSelector((state) => state.common.isMobile);

  function onClickUnstake() {
    off();
    nextProcess();
  }

  if (isMobile)
    return (
      <>
        <MunstakeLiquidityPopup>
          <article className="topBar">
            <strong className="title">Unstake liquidity</strong>

            <button className="exitBtn" onClick={() => off()}>
              <img src={I_xWhite} alt="" />
            </button>
          </article>

          <article className="contArea">
            <div className="contBox">
              <img src={E_item} alt="" />
              <p>Friends #12546</p>
            </div>

            <button className="unstakeBtn" onClick={onClickUnstake}>
              Unstake
            </button>
          </article>
        </MunstakeLiquidityPopup>
      </>
    );
  else
    return (
      <>
        <PunstakeLiquidityPopup>
          <article className="topBar">
            <span className="blank" />

            <strong className="title">Unstake liquidity</strong>

            <button className="exitBtn" onClick={() => off()}>
              <img src={I_xWhite} alt="" />
            </button>
          </article>

          <article className="contArea">
            <div className="contBox">
              <img src={E_item} alt="" />
              <p>Friends #12546</p>
            </div>

            <button className="unstakeBtn" onClick={onClickUnstake}>
              Unstake
            </button>
          </article>
        </PunstakeLiquidityPopup>
      </>
    );
}

